import React from 'react';
import { connect } from 'dva';
import { List } from 'antd';
import BaseComponent from 'components/BaseComponent';
import Icon from 'components/Icon';
import SideLayout from '../../components/SideLayout';

@connect()
export default class extends BaseComponent {
  state = {
    chartTypes: [
      { title: ' / Line', icon: 'line-chart' },
      { title: ' / Bar', icon: 'bar-chart' },
      { title: ' / Pie', icon: 'pie-chart' },
      { title: ' / Scatter', icon: 'dot-chart' },
      { title: ' / Map', icon: 'global' },
      { title: ' / Radar', icon: 'trademark' },
      { title: ' / gauge', icon: 'dashboard' }
    ]
  };

  render() {
    const { chartTypes } = this.state;
    const sideContent = (
      <List
        className="charts-type-list"
        dataSource={chartTypes}
        renderItem={item => (
          <List.Item actions={[<Icon type="ellipsis" antd />]}>
            <Icon type={item.icon} antd />
            {item.title}
          </List.Item>
        )}
      />
    );
    return (
      <SideLayout
        title="ECharts "
        author=" ECharts "
        site="http://echarts.baidu.com"
        sideContent={sideContent}
      />
    );
  }
}



